import React from 'react';
import Playground from 'component-playground';
import Button from "./components/button";

require("./styles/syntax.css");
require("./styles/codemirror.css");
require("./styles/demo.css");

const es6Example = "const foo = function* (x) {\n" +
    "  const y = yield x + 1;\n" +
    "\n" +
    "  return y + 2;\n" +
    "}\n" +
    "\n" +
    "const bar = foo(3);\n" +
    "\n" +
    "console.log(bar.next().value);\n" +
    "console.log(bar.next(5).value);\n" +
    "\n" +
    "const end = [3, 4];\n" +
    "\n" +
    "console.log([1, 2, ...end]);\n" +
    "\n" +
    "const a = \"a\";\n" +
    "const b = \"b\";\n" +
    "\n" +
    "console.log({a, b});\n" +
    "console.log([\"a\", \"b\"]);\n" +
    "console.log(\"a\");\n" +
    "console.log(function () {});\n" +
    "console.log(null);\n" +
    "console.log(undefined);";

function Editor() {
    return (
        <div className="component-documentation">
            <h2>ES6 Console</h2>

            <Playground
                codeText={es6Example}
                es6Console
                scope={{ React: React, Button: Button }}
            />
        </div>
    );
}

export default Editor;
